/* eslint-disable */
import React from 'react';
import '../Styles/Bloc.css';
import TextareaAutosize from 'react-textarea-autosize';


function Bloc({ change, val, keyed, id, autof, delet, styles }) {

  const handleKeyPress = (e) => {

    if (e.nativeEvent.keyCode === 13) {         
        if(!e.nativeEvent.shiftKey){
          if (val === "/page") {
            console.log("CREEr baTARD");
            e.preventDefault();
          } else {
            keyed(id);
            e.preventDefault();
          }
        }


    } else if (e.nativeEvent.keyCode === 8) {
      if (val === "" && id !== 0) {
        delet(id);
      }
    }
  }

  if (autof) {
    return <div >
    <TextareaAutosize onKeyDownCapture={ev => {
      handleKeyPress(ev);
    }} id='bloc' value={val} onChange={change} 
    spellCheck={false} style={styles} autoFocus/>
  </div>
  } else {
    return <div>
    <TextareaAutosize onKeyDownCapture={ev => {
      handleKeyPress(ev);
    }} id='bloc' style={styles} value={val} onChange={change} 
    spellCheck={false}/>
  </div>
  }

}

export default Bloc;