/* eslint-disable */
import React, { useRef } from 'react'
import Bloc from './Components/Bloc';
import TextareaAutosize from 'react-textarea-autosize';
import MyComponent from './Components/ContextMenu';
import ToolBarCss from './Styles/ToolBar.css'

class Main extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      menu: false,
      targetedBloc: 0,
      x: 0,
      y: 0,
      id: '',
      title: '',
      ref: '',
      contente:'',
      body: [
        {
        }
      ]
    };
    this.shortcut = {
      shorts: [
        {
          brut: "ajd",
          replaced: "aujourd'hui"
        },
        {
          brut: "slt",
          replaced: "salut"
        },
        {
          brut: "/lorem ",
          replaced: "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec convallis mollis molestie. Nunc volutpat et metus nec convallis. Phasellus velit felis, maximus sit amet faucibus sed, molestie et metus. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Vivamus et ante sed nisl vestibulum posuere. Pellentesque urna tellus, ornare dapibus vehicula eu, dapibus id arcu. Donec venenatis eget lacus at blandit."
        }
        ,
        {
          brut: "/raphouel ",
          replaced: "On veut pas baiser les gos les plus putes, mais boire les eaux les plus pures"
        },
        {
          brut: "quoi",
          replaced: "feur"
        }
      ]
    }
  }

  handleSetRed = (param) => (e) => {

    let oldStyle = param.style;

    let color2 = {
      "color": "red"
    }

    let color = {...oldStyle, ...color2};
    let index = param.id;
    let newBody = this.state.body;
    newBody[index].style = color;
    console.log(newBody);
    this.setState({body: newBody});
    this.props.activeNote.body[index].style = color;
  }

  handleSetBlack = (param) => (e) => {

    let oldStyle = param.style;

    let color2 = {
      "color": "black"
    }

    let color = {...oldStyle, ...color2};
    let index = param.id;
    let newBody = this.state.body;
    newBody[index].style = color;
    console.log(newBody);
    this.setState({body: newBody});
    this.props.activeNote.body[index].style = color;
  }

  handleSetGreen = (param) => (e) => {

    let oldStyle = param.style;

    let color2 = {
      "color": "green"
    }

    let color = {...oldStyle, ...color2};
    let index = param.id;
    let newBody = this.state.body;
    newBody[index].style = color;
    console.log(newBody);
    this.setState({body: newBody});
    this.props.activeNote.body[index].style = color;
  }

  handleSetBlue = (param) => (e) => {

    let oldStyle = param.style;

    let color2 = {
      "color": "blue"
    }

    let color = {...oldStyle, ...color2};
    let index = param.id;
    let newBody = this.state.body;
    newBody[index].style = color;
    console.log(newBody);
    this.setState({body: newBody});
    this.props.activeNote.body[index].style = color;
  }

  handelSetWhite = (param) => (e) => {

    let oldStyle = param.style;

    let color2 = {
      "color": "white"
    }

    let color = {...oldStyle, ...color2};
    let index = param.id;
    let newBody = this.state.body;
    newBody[index].style = color;
    console.log(newBody);
    this.setState({body: newBody});
    this.props.activeNote.body[index].style = color;
  }

  handleContextMenu = (param) => (event) => {

    /*
    event.preventDefault();
    this.setState({menu: true});
    this.setState({x: event.clientX});
    this.setState({y: event.clientY});

    */
    event.preventDefault();
    this.setState({menu: true});
    this.setState({x: event.clientX});
    this.setState({y: event.clientY});

    this.setState({targetedBloc: param.id});

  }

  handleCloseMenu = () => {
    this.setState({menu: false});
  }

  componentDidMount() {
    this.updateNoteData(this.props.activeNote);
  }

  componentDidUpdate(prevProps) {
    if (prevProps.activeNote !== this.props.activeNote) {
      this.updateNoteData(this.props.activeNote);
      console.log(this.props.activeNote);
    }
  }

  updateTitle = (evt) => {
    const val = evt.target.value;
    if (val) {
        this.setState({title: val});
        this.props.activeNote.title = val;
    }
  }

  updateNoteData = (activeNote) => {
    this.setState({
        id: activeNote.id,
        title: activeNote.title,
        body: activeNote.body,
        contente: activeNote.contente
    });

  }

  handleTitleChange = (event) => {
    this.setState({ title: event.target.value });
  }

  handleListener = (index) => {
    let val = this.state.body[index].content;


    this.shortcut.shorts.map((el) => {
      if(val.includes(el.brut)) {
        let replaced = this.state.body[index].content.replace(el.brut, el.replaced);
        this.state.body[index].content = replaced;
      }
    })
    /*
    if (val.includes("ajd")) {
      let replaced = this.state.body[index].content.replace("ajd", "aujourd'hui");
      this.state.body[index].content = replaced;
    }
    */
  }

  handleBodyChange = (event, index) => {

    let newBody = this.state.body;
    newBody[index].content = event.target.value;
    this.setState({body: newBody});
    this.props.activeNote.body[index].content = event.target.value

    this.handleListener(index);
  }

  underline = () => {
    let newBody = this.state.body;
    newBody[0].style = {
      "color": "red"
    }

    this.setState({body: newBody});
    this.props.activeNote.body[0] = newBody;
  }

  handleDeleteBlock = (current) => {
    let oldBody = this.state.body;
    console.log(oldBody);
    const removed = oldBody.splice(current, 1);
    console.log(removed);

    this.setState({body: oldBody});
    this.props.activeNote.body = oldBody;


  }

  handleAddBlock = (current) => {
    let actual = current + 1;
    this.setState({ref: actual});

    console.log(current);
    console.log(this.state.body);


    let newBody = this.state.body;
    let avaibleIndex = this.state.body.length;
    let newBlock = {
        id: avaibleIndex,
        content: '',
        style: {
          
        }
    }

    newBody.splice(current + 1, 0, newBlock);

    this.setState({body: newBody});
    this.props.activeNote.body = newBody;

  }

  alignLeft = (param) => (e) => {

    let oldStyle = param.style;

    let color2 = {
      "textAlign": "left"
    }

    let color = {...oldStyle, ...color2};
    let index = param.id;
    let newBody = this.state.body;
    newBody[index].style = color;
    console.log(newBody);
    this.setState({body: newBody});
    this.props.activeNote.body[index].style = color;
  }

  alignCenter = (param) => (e) => {

    let oldStyle = param.style;


    let color2 = {
      "textAlign": "center"
    }

    let color = {...oldStyle, ...color2};
    let index = param.id;
    let newBody = this.state.body;
    newBody[index].style = color;
    console.log(newBody);
    this.setState({body: newBody});
    this.props.activeNote.body[index].style = color;
  }

  alignRight = (param) => (e) => {

    let oldStyle = param.style;

    let color2 = {
      "textAlign": "right"
    }

    let color = {...oldStyle, ...color2};
    let index = param.id;
    let newBody = this.state.body;
    newBody[index].style = color;
    console.log(newBody);
    this.setState({body: newBody});
    this.props.activeNote.body[index].style = color;
  }

  setBold = (param) => (e) => {

    let oldStyle = param.style;

    if (oldStyle.fontWeight === "normal") {
      console.log("non");

      let color2 = {
        "fontWeight": "bold"
      }

      let color = {...oldStyle, ...color2};
      let index = param.id;
      let newBody = this.state.body;
      newBody[index].style = color;
      console.log(newBody);
      this.setState({body: newBody});
      this.props.activeNote.body[index].style = color;

    } else {
      let color2 = {
        "fontWeight": "normal"
      }

      let color = {...oldStyle, ...color2};
      let index = param.id;
      let newBody = this.state.body;
      newBody[index].style = color;
      console.log(newBody);
      this.setState({body: newBody});
      this.props.activeNote.body[index].style = color;
    }
    /*
    let color2 = {
      "fontWeight": "bold"
    }

    let color = {...oldStyle, ...color2};
    let index = param.id;
    let newBody = this.state.body;
    newBody[index].style = color;
    console.log(newBody);
    this.setState({body: newBody});
    this.props.activeNote.body[index].style = color;

    */
  }

  setUnderline = (param) => (e) => {

    let oldStyle = param.style;

    if (oldStyle.textDecoration === "underline") {
      let color2 = {
        "textDecoration": "none"
      }

      let color = {...oldStyle, ...color2};
      let index = param.id;
      let newBody = this.state.body;
      newBody[index].style = color;
      console.log(newBody);
      this.setState({body: newBody});
      this.props.activeNote.body[index].style = color;

    } else {
      let color2 = {
        "textDecoration": "underline"
      }

      let color = {...oldStyle, ...color2};
      let index = param.id;
      let newBody = this.state.body;
      newBody[index].style = color;
      console.log(newBody);
      this.setState({body: newBody});
      this.props.activeNote.body[index].style = color;
    }
  }

  render() {


    return (
        <div className="app-body" onClick={this.handleCloseMenu}>
            <div className="app-body-title">
                <TextareaAutosize
                    id="title"
                    placeholder="Title"
                    value={this.state.title}
                    onChange={this.handleTitleChange}
                    onKeyUp={evt => { this.updateTitle(evt);
                        this.props.setActiveNote(this.state.id);
                    }}/>
            </div>
            <div className="app-body-markdown">
                {this.props.activeNote.body.map((blc, index) => {
                    //return <textarea value={this.state.body[blc.id].content} onChange={ev => {this.handleBodyChange(ev, blc.id)}}></textarea>
                    if (index === this.state.ref) {
                        console.log(blc);
                        let compt = (
                          <div onContextMenu={this.handleContextMenu(blc)} ref={this.ref}>
                            {this.state.menu && (
                              <div id="toolbar" style={(this.state.targetedBloc === blc.id) ? ToolBarCss : {visibility: "hidden", height: "0px"}} onClick={this.handleCloseMenu}>
                              <div id="colors">
                                  <div onClick={this.handleSetRed(blc)} id="red"></div>
                                  <div onClick={this.handleSetBlue(blc)} id="blue"></div>
                                  <div onClick={this.handleSetGreen(blc)} id="green"></div>
                                  <div onClick={this.handleSetBlack(blc)} id="black"></div>
                                  <div onClick={this.handelSetWhite(blc)} id="white"></div>
                                  <div id="decoration">
                                    <img onClick={this.setUnderline(blc)} src={require('./Images/Toolbar/alignment/underline.png')}></img>
                                    <img onClick={this.setBold(blc)} src={require('./Images/Toolbar/alignment/bold.png')}></img>
                                  </div>
                                  <div id="alignment">
                                    <img onClick={this.alignLeft(blc)} src={require("./Images/Toolbar/alignment/left.png")}></img>
                                    <img onClick={this.alignCenter(blc)} src={require("./Images/Toolbar/alignment/center.png")}></img>
                                    <img onClick={this.alignRight(blc)} src={require("./Images/Toolbar/alignment/right.png")}></img>
                                  </div>
                            </div>
                        </div>
                              )}
                            <Bloc
                              keyed={this.handleAddBlock}
                              id={index}
                              onClick={"l'index est : " +console.log(index)}
                              val={blc.content}
                              change={ev => {this.handleBodyChange(ev, index)}}
                              ref={this.comptRef}
                              autof={true}
                              delet={this.handleDeleteBlock}
                              styles={blc.style}
                            />
                            </div>
                          );
                        return compt;
                    } else {
                        let compt = (
                            <div onContextMenu={this.handleContextMenu(blc)}>
                              {this.state.menu && (
                              <div id="toolbar" style={(this.state.targetedBloc === blc.id) ? ToolBarCss : {visibility: "hidden", height: "0px"}} onClick={this.handleCloseMenu}>
                                <div id="colors">
                                  <div onClick={this.handleSetRed(blc)} id="red"></div>
                                  <div onClick={this.handleSetBlue(blc)} id="blue"></div>
                                  <div onClick={this.handleSetGreen(blc)} id="green"></div>
                                  <div onClick={this.handleSetBlack(blc)} id="black"></div>
                                  <div onClick={this.handelSetWhite(blc)} id="white"></div>
                                  <div id="decoration">
                                    <img onClick={this.setUnderline(blc)} src={require('./Images/Toolbar/alignment/underline.png')}></img>
                                    <img onClick={this.setBold(blc)} src={require('./Images/Toolbar/alignment/bold.png')}></img>
                                  </div>
                                  <div id="alignment">
                                    <img onClick={this.alignLeft(blc)} src={require("./Images/Toolbar/alignment/left.png")}></img>
                                    <img onClick={this.alignCenter(blc)} src={require("./Images/Toolbar/alignment/center.png")}></img>
                                    <img onClick={this.alignRight(blc)} src={require("./Images/Toolbar/alignment/right.png")}></img>
                                  </div>
                              </div>
                          </div>
                              )}
                              <Bloc
                              keyed={this.handleAddBlock}
                              id={index}
                              val={blc.content}
                              change={ev => {this.handleBodyChange(ev, index)}}
                              autof={false}
                              delet={this.handleDeleteBlock}
                              styles={blc.style}
                              />
                            </div>
                          ); 
                        return compt;
                    }
                })}
            </div>
        </div>
    );
}
}

export default Main;